/*eslint-disable no-unused-vars*/

import React from 'react'
import { connect } from 'react-redux'
import styled, { keyframes } from 'styled-components'
import mvConsts from '../../../constants/mvConsts'
import Container from '../../Container'
import Button from '../UI/Button'
import moment from 'moment'
import uiActions from '../../../redux/actions/UIActions'

let daysToSelect = [], time_sets = []
for (let i = 0; i < 14; i++) { daysToSelect.push(i) }
for (let i = 0; i < 24 / 0.5; i++) { time_sets.push(i) }
let weekDays = [
    ``,
    ``,
    ``,
    ``,
    ``,
    ``,
    ``,
]

class ClubScreen extends React.Component {
    state = {
        select_mode: undefined,
        selected_slot: undefined,
    }
    render = () => {
        return (
            <Wrapper>
                <Container extraProps={`width: 94vw; height: 10vh; flex-direction: row`} >
                    <Container extraProps={`width: 6vw; height: 10vh; border-bottom: 0.1vw solid ${mvConsts.colors.background.support}; `} >

                    </Container>
                    <Container extraProps={`width: 88vw; height: 10vh; flex-direction: row; border-bottom: 0.1vw solid ${mvConsts.colors.background.support}; `} >
                        {
                            time_sets.filter(i => i % 2 === 0).map((i, index) => {
                                return (
                                    <Container key={index} extraProps={`width: ${88 / time_sets.length * 2}vw; color: ${mvConsts.colors.text.primary}; font-size: 0.8vw; `} >
                                        {moment().startOf(`day`).add(index, `hour`).format(`HH:mm`)}
                                    </Container>
                                )
                            })
                        }
                    </Container>
                </Container>
                <Container extraProps={`width: 94vw; height: 80vh; display: block; overflow: auto; max-height: 80vh; `} >
                    {
                        daysToSelect.map((i, day_index) => {
                            let startOfDay = moment().startOf(`day`).add(day_index, `day`)
                            let isWeekEnd = moment(startOfDay).day() > 5 || moment(startOfDay).day() === 0
                            return (
                                <Container extraProps={`flex-direction: row; position: relative;`} key={day_index} >
                                    <Container extraProps={`width: 6vw; height: 10vh; font-size: 0.8vw; color: ${isWeekEnd ? `white` : mvConsts.colors.text.primary}; background-color: ${isWeekEnd ? mvConsts.colors.yellow : null}; border-right: 0.1vw solid ${mvConsts.colors.background.support}; `} >
                                        {moment(startOfDay).format(`DD.MM`)}, {weekDays[moment(startOfDay).day()]}
                                    </Container>
                                    {
                                        this.state.selected_slot ? this.state.selected_slot.day === day_index ?
                                            <Container extraProps={`width: ${88 / time_sets.length * (this.state.selected_slot.to - this.state.selected_slot.from + 1)}vw; height: 9.6vh; background-color: ${mvConsts.colors.yellow}; border-radius: 0.5vw; position: absolute; z-index: 2; left: ${6 + this.state.selected_slot.from * 88 / time_sets.length}vw; top: 0.3vh;`} >

                                            </Container>
                                            : null : null
                                    }
                                    <Container extraProps={`width: 88vw; height: 10vh; flex-direction: row; border-top: 0.1vw solid ${mvConsts.colors.background.support}; `} >
                                        {
                                            time_sets.map((i, index) => {
                                                return (
                                                    <Container
                                                        key={index}
                                                        extraProps={`
                                                            width: ${88 / time_sets.length}vw;
                                                            height: 9.6vh;
                                                            margin: 0.2vh 0 0.2vh 0
                                                            background-color: ${
                                                            this.state.select_mode
                                                                ? this.state.select_mode.day === day_index
                                                                    && index >= this.state.select_mode.from
                                                                    && index <= this.state.select_mode.to
                                                                    ? mvConsts.colors.background.primary
                                                                    : null
                                                                : null};
                                                            border-top-left-radius: ${this.state.select_mode ? this.state.select_mode.from === index ? 0.5 : 0 : 0}vw;
                                                            border-bottom-left-radius: ${this.state.select_mode ? this.state.select_mode.from === index ? 0.5 : 0 : 0}vw;
                                                            border-top-right-radius: ${this.state.select_mode ? this.state.select_mode.to === index ? 0.5 : 0 : 0}vw;
                                                            border-bottom-right-radius: ${this.state.select_mode ? this.state.select_mode.to === index ? 0.5 : 0 : 0}vw;
                                                            &:hover {
                                                                background-color: ${mvConsts.colors.background.primary};
                                                                border-top-left-radius: ${this.state.select_mode ? this.state.select_mode.from === index ? 0.5 : 0 : 0.5}vw;
                                                                border-bottom-left-radius: ${this.state.select_mode ? this.state.select_mode.from === index ? 0.5 : 0 : 0.5}vw;
                                                                border-top-right-radius: ${this.state.select_mode ? this.state.select_mode.to === index ? 0.5 : 0 : 0.5}vw;
                                                                border-bottom-right-radius: ${this.state.select_mode ? this.state.select_mode.to === index ? 0.5 : 0 : 0.5}vw;
                                                            };
                                                        `}
                                                        onMouseOver={() => {
                                                            if (this.state.select_mode) {
                                                                if (this.state.select_mode.from <= index) {
                                                                    this.setState({ select_mode: { ...this.state.select_mode, to: index } })
                                                                }
                                                            }
                                                        }}
                                                        onClick={() => {
                                                            if (this.state.select_mode) {
                                                                this.setState({
                                                                    selected_slot: this.state.select_mode,
                                                                    select_mode: undefined,
                                                                })
                                                                this.props.setPopUpWindow(mvConsts.popUps.CLUB_BOOK)
                                                            } else {
                                                                this.setState({
                                                                    selected_slot: this.state.select_mode,
                                                                    select_mode: { day: day_index, from: index, to: index },
                                                                })
                                                            }
                                                        }}
                                                    >

                                                    </Container>
                                                )
                                            })
                                        }
                                    </Container>
                                </Container>
                            )
                        })
                    }
                </Container>
            </Wrapper>
        )
    }
}

const Wrapper = styled.div`
display: flex
justify-content: center
align-items: center
flex-direction: column
width: 94vw
height: 90vh
transition: 0.2s
@media (min-width: 320px) and (max-width: 480px) {
    flex-direciton: column;
}
`
const Header = styled.div`
display: flex
justify-content: center
align-items: center
flex-direction: column
width: 94vw
height: 10vh
transition: 0.2s
`

let mapStateToProps = (state) => {
    return {
        server_time: state.constants.server_time,
    }
}

let mapDispatchToProps = (dispatch) => {
    return {
        setPopUpWindow: (data) => {
            return dispatch(uiActions.setPopUpWindow(data))
        },
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(ClubScreen)

/*eslint-enable no-unused-vars*/